*{
    box-sizing: border-box;
}
html{
    scroll-behavior: smooth;
}
body{
    font-family: 'Cairo', sans-serif;
    font-family: 'Work Sans', sans-serif;
    margin: 0;
    padding: 0;
}
.container{
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;

}

@media (min-width: 768px ){
    .container{
        width: 750px;
    }
}

@media (min-width: 993px ){
    .container{
        width: 970px;
    }
}
@media (min-width: 1200px ){
    .container{
        width: 1170px;
    }
}