.image{
    width: 100%;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}
.card{
    border-radius: 10px;
    box-shadow: 0 0 4px gray;
}
h3{
    margin: 10px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 6px;
}
.button{
    margin: 20px auto;
    display: flex;
    align-items: center;
    justify-content: center;
}
.button button{
    background-color: rgb(58, 31, 122);
    padding: 10px 20px;
    border: none;
    color: white;
    border-radius: 20px;
    cursor: pointer;
}