.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 0 20px 0;
    
}
.hh{
    box-shadow: 0 0 10px gray;
    padding: 0;
}
ul{
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
ul li{
    margin-left: 20px;
    color: black;
    font-weight: bold;
}
.link{
    text-decoration: none;
    
}
